@media screen and (max-width: 768px) {
    .logo{
        display:none;
    }

    .HomeContent{
        height:70vh;
    }
    .Home{
    background-position: center;
    }
    .About{
        background-image: url('./aboutmeMobile.jpeg');
        height:100%;
          background-repeat: no-repeat;
          background-size: cover;
          width:auto;
          background-position: left;
          filter: brightness(90%);
    }
}
@media screen and (min-width: 768px) {
    .Home{
        background-image: url('./HomePageImageCut.jpg');
        height:70vh;
          background-repeat: no-repeat;
          background-size: cover;
          width:auto;
    }
    .About{
        background-image: url('./aboutmeCut.jpeg');
        height:100%;
          background-repeat: no-repeat;
          background-size: cover;
          width:auto;
          background-position: right;
          filter: brightness(90%);
    }
    .aboutText{
        font-size: 1.2rem;
    }
    
}

@media screen and (min-width: 992px) {
    .Home{
        background-image: url('./HomePageImage.jpg');
        height:70vh;
          background-repeat: no-repeat;
          background-size: cover;
          width:auto;
    }

    .About{
        background-image: url('./aboutme.jpeg');
        height:100%;
          background-repeat: no-repeat;
          background-size: cover;
          width:auto;
          background-position: right;
          filter: brightness(90%);
    }

    .aboutText{
        font-size: 1.3rem;
    }
}



